const APIUtil = {
  followUser: id => APIUtil.changeFollowStatus(id, "POST"),
  unfollowUser: id => APIUtil.changeFollowStatus(id, "DELETE"),

  changeFollowStatus: (id, method) => {
    return $.ajax({
      url: `/users/${id}/follow`,
      method: method,
      dataType: "JSON",
      data: {
        user_id: id
      }
    });
  }
};

module.exports = APIUtil;



